.container_header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  padding: 1% 3%;
  height: 85px;
  background-color: #f8f9fc;
  z-index: 1;
}
.header__container-logo {
  display: flex;
  align-items: center;
  width: 20%;
}
.header_logo {
  height: 60px;
}

.container_menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 35%;
}

.container_login {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 10%;
}

.menu,
.header__p-number {
  font-family: "Montserrat";
  font-weight: 600;
  color: black;
  font-style: normal;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.header__p-number {
  font-size: 14px;
}

.header_container-search {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 30%;
}

.header_container-number {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 45%;
}

.header__container-input-img {
  display: flex;
  align-items: center;
  height: 100%;
  width: 50%;
}

.header__container-input {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border: 2px solid #a1a1a1;
  box-sizing: border-box;
  border-radius: 20px;
  height: 40px;
  width: 90%;
}

.header__input {
  width: 60%;
  border-radius: 20px;
  outline: none;
  border: none;
}

.header__img,
.header__img-focus {
  width: 40px;
  height: 40px;
}

.header__img-search {
  height: 15px;
}

.header__img-focus:hover {
  cursor: pointer;
}

@media screen and (max-width: 1250px) {
  .menu {
    font-size: 11px;
  }
  .header__p-number {
    font-size: 11px;
  }
}
